import { useQuery } from "@tanstack/react-query";
import styled from "styled-components";
import {
  getAirSeries,
  getImg,
  getPopularSeries,
  getSeries,
  getSeriesDetail,
  getTopSeries,
  ISeries,
} from "../api";
import { AnimatePresence, motion, useScroll } from "framer-motion";
import { useState } from "react";
import { useHistory, useRouteMatch } from "react-router-dom";
import Details from "../Components/Details";
import SeriesSlider from "../Components/SeriesSlider";
import SeriesDetails from "../Components/SeriesDetails";

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  overflow-x: hidden;
`;
const Loader = styled.div`
  font-size: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Banner = styled.div<{ imgPath: string | undefined }>`
  width: 100vw;
  padding: 0 60px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 1)
    ),
    url(${(props) => props.imgPath});

  background-size: cover;
`;

const Title = styled.h1`
  font-size: 80px;
  margin-bottom: 15px;
`;

const Overview = styled.p`
  width: 35%;
  font-size: 23px;
  margin-bottom: 20px;
`;

const PlayBtn = styled.div`
  width: 200px;
  height: 70px;
  padding: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: ${(props) => props.theme.white.darker};
  cursor: pointer;
  svg {
    width: 37px;
    margin-right: 30px;
  }
  span {
    color: black;
    font-size: 32px;
  }
`;

const Overlay = styled(motion.div)`
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  opacity: 0;
  position: fixed;
  top: 0;
`;
const DetailWrapper = styled(motion.div)`
  width: 40vw;
  height: 80vh;
  background-color: ${(props) => props.theme.black.darker};
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
`;

const offset = 6;

function Tv() {
  const [todayIndex, setTodayIndex] = useState(0);
  const [airIndex, setAirIndex] = useState(0);
  const [popularIndex, setPopularIndex] = useState(0);
  const [topIndex, setTopIndex] = useState(0);
  const tvMatch = useRouteMatch<{ seriesId: string }>("/tv/:seriesId");
  const history = useHistory();
  const { data: seriesData, isLoading: seriesLoading } = useQuery<ISeries>({
    queryKey: ["series", "Today"],
    queryFn: getSeries,
  });
  const { data: airData, isLoading: airLoading } = useQuery<ISeries>({
    queryKey: ["series", "On The Air"],
    queryFn: getAirSeries,
  });
  const { data: popularData, isLoading: popularLoading } = useQuery<ISeries>({
    queryKey: ["series", "Popular"],
    queryFn: getPopularSeries,
  });
  const { data: topData, isLoading: topLoading } = useQuery<ISeries>({
    queryKey: ["movies", "Top Rated"],
    queryFn: getTopSeries,
  });

  const prevSlide = () => {
    setTodayIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextSlide = () => {
    if (seriesData) {
      const maxIndex = Math.ceil(seriesData?.results.length / offset) - 1;
      setTodayIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const prevAir = () => {
    setAirIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextAir = () => {
    if (airData) {
      const maxIndex = Math.ceil(airData?.results.length / offset) - 1;
      setAirIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const prevPopular = () => {
    setPopularIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextPopular = () => {
    if (popularData) {
      const maxIndex = Math.ceil(popularData?.results.length / offset) - 1;
      setPopularIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const prevTop = () => {
    setTopIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextTop = () => {
    if (seriesData) {
      const maxIndex = Math.ceil(seriesData?.results.length / offset) - 1;
      setTopIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const backToMain = () => {
    history.push("/tv");
  };
  const showBigScreen = (cat: string, id: number) => {
    history.push(`/tv/${cat}${id}`);
  };
  const { scrollY } = useScroll();

  return (
    <>
      {seriesLoading ? (
        <Loader>
          <h1>Loading...</h1>
        </Loader>
      ) : (
        <Wrapper>
          <>
            <Banner
              imgPath={getImg(seriesData?.results[0].backdrop_path || "")}
            >
              <Title>{seriesData?.results[0].name}</Title>
              <Overview>{seriesData?.results[0].overview}</Overview>
              <PlayBtn>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512">
                  <path d="M73 39c-14.8-9.1-33.4-9.4-48.5-.9S0 62.6 0 80L0 432c0 17.4 9.4 33.4 24.5 41.9s33.7 8.1 48.5-.9L361 297c14.3-8.7 23-24.2 23-41s-8.7-32.2-23-41L73 39z" />
                </svg>
                <span>Play</span>
              </PlayBtn>
            </Banner>
            {seriesData && (
              <SeriesSlider
                series={seriesData.results.slice(1)}
                index={todayIndex}
                next={nextSlide}
                prev={prevSlide}
                category="Today"
                showBigScreen={showBigScreen}
              />
            )}
            {popularLoading ? (
              <Loader>
                <h1> Loading...</h1>
              </Loader>
            ) : (
              popularData && (
                <SeriesSlider
                  series={popularData.results}
                  index={popularIndex}
                  next={nextPopular}
                  prev={prevPopular}
                  category="Popular"
                  showBigScreen={showBigScreen}
                />
              )
            )}

            {airLoading ? (
              <Loader>
                <h1>Loading...</h1>
              </Loader>
            ) : (
              airData && (
                <SeriesSlider
                  series={airData.results}
                  index={airIndex}
                  next={nextAir}
                  prev={prevAir}
                  category="On The Air"
                  showBigScreen={showBigScreen}
                />
              )
            )}
            {topLoading ? (
              <Loader>
                <h1> Loading...</h1>
              </Loader>
            ) : (
              topData && (
                <SeriesSlider
                  series={topData.results}
                  index={topIndex}
                  next={nextTop}
                  prev={prevTop}
                  category="Top Rated"
                  showBigScreen={showBigScreen}
                />
              )
            )}

            <AnimatePresence>
              {tvMatch ? (
                <>
                  <Overlay
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                    // without this, frame looks stiff.
                    onClick={backToMain}
                  />
                  <DetailWrapper
                    style={{
                      top: scrollY.get() + 50,
                      overflowY: "auto",
                    }}
                    layoutId={tvMatch.params.seriesId}
                  >
                    <SeriesDetails id={tvMatch.params.seriesId.slice(3)} />
                  </DetailWrapper>
                </>
              ) : null}
            </AnimatePresence>
          </>
        </Wrapper>
      )}
    </>
  );
}

export default Tv;
