import { useQuery } from "@tanstack/react-query";
import { useHistory, useLocation, useRouteMatch } from "react-router-dom";
import { getImg, IMulti, searchMulti } from "../api";
import styled from "styled-components";
import { AnimatePresence, motion, useScroll } from "framer-motion";
import Details from "../Components/Details";
import SeriesDetails from "../Components/SeriesDetails";

const Wrapper = styled.div`
  padding: 200px 50px 50px 50px;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  column-gap: 10px;
  row-gap: 80px;
`;
const Loader = styled.div`
  font-size: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Card = styled(motion.div)`
  color: black;
  height: 170px;
  border-radius: 5px;
  cursor: pointer;
  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
  &:nth-child(6n + 1) {
    transform-origin: left center;
  }
  &:nth-child(6n) {
    transform-origin: right center;
  }
`;

const Info = styled(motion.div)`
  width: 100%;
  padding: 10px;
  text-align: center;
  background-color: ${(props) => props.theme.black.lighter};
  opacity: 0;
  color: ${(props) => props.theme.white.darker};
  position: absolute;
  bottom: -40px;
`;

const infoVar = {
  hover: {
    opacity: 1,
  },
};

const cardVar = {
  initial: {
    scale: 1,
  },
  hover: {
    scale: 1.5,
    y: -50,

    transition: {
      delay: 0.5,
      type: "tween",
      duration: 0.2,
    },
  },
};

const Overlay = styled(motion.div)`
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  opacity: 0;
  position: fixed;
  top: 0;
`;
const DetailWrapper = styled(motion.div)`
  width: 40vw;
  height: 80vh;
  background-color: ${(props) => props.theme.black.darker};
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
`;

function Search() {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const keyword = params.get("keyword");
  const history = useHistory();
  const { scrollY } = useScroll();
  const { data, isLoading } = useQuery<IMulti>({
    queryKey: ["search", keyword],
    queryFn: () => searchMulti(keyword || ""),
  });
  const showBigScreen = (id: number, type: string) => {
    history.push(`/search?keyword=${keyword}&type=${type}&id=${id}`);
  };
  const searchId = params.get("id");
  const mediaType = params.get("type");
  const backToMain = () => {
    history.push(`/search?keyword=${keyword}`);
  };
  return (
    <Wrapper>
      <>
        {isLoading ? (
          <Loader>
            <h1>Loading...</h1>
          </Loader>
        ) : (
          data?.results.map((item) => (
            <Card
              layoutId={item.id + ""}
              onClick={() => showBigScreen(item.id, item.media_type)}
              variants={cardVar}
              initial="initial"
              whileHover="hover"
              transition={{ type: "tween" }}
              key={item.id}
            >
              <motion.img
                src={getImg(item.poster_path || item.backdrop_path, "w500")}
                alt="poster"
              />
              <Info variants={infoVar}>
                <h4>{item.title ? item.title : item.name}</h4>
              </Info>
            </Card>
          ))
        )}
        <AnimatePresence>
          {searchId ? (
            <>
              <Overlay
                animate={{ opacity: 1 }}
                exit={{ opacity: 0 }}
                // without this, frame looks stiff.
                onClick={backToMain}
              />
              <DetailWrapper
                style={{ top: scrollY.get() + 50, overflowY: "auto" }}
                layoutId={searchId}
              >
                {mediaType === "movie" ? (
                  <Details id={searchId} />
                ) : (
                  <SeriesDetails id={searchId} />
                )}
              </DetailWrapper>
            </>
          ) : null}
        </AnimatePresence>
      </>
    </Wrapper>
  );
}

export default Search;
