import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import styled from "styled-components";
import { getImg, IMovie, IResult, useWindowWidth } from "../api";

const Wrapper = styled.div`
  position: relative;
  top: -100px;
  height: 200px;
  margin-bottom: 100px;
  h1 {
    position: absolute;
    top: -50px;
    left: 50px;
    font-size: 32px;
    margin-left: 15px;
  }
`;
const Row = styled(motion.div)`
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  gap: 5px;
  width: 90%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
`;

const Card = styled(motion.div)`
  background-color: white;
  color: black;
  height: 200px;
  border-radius: 5px;
  cursor: pointer;
  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    border-radius: 5px;
  }
  &:first-child {
    transform-origin: left center;
  }
  &:last-child {
    transform-origin: right center;
  }
`;

const Info = styled(motion.div)`
  width: 100%;
  padding: 10px;
  text-align: center;
  background-color: ${(props) => props.theme.black.lighter};
  opacity: 0;
  color: ${(props) => props.theme.white.darker};
  position: absolute;
  bottom: -40px;
`;

const SlideBtn = styled(motion.div)`
  width: 60px;
  height: 200px;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  cursor: pointer;
  svg {
    width: 40px;
    fill: rgba(255, 255, 255, 0.5);
  }
`;

const infoVar = {
  hover: {
    opacity: 1,
  },
};

const cardVar = {
  initial: {
    scale: 1,
  },
  hover: {
    scale: 1.5,
    y: -50,

    transition: {
      delay: 0.5,
      type: "tween",
      duration: 0.2,
    },
  },
};

interface ISlider {
  movies: IResult[];
  index: number;
  next: Function;
  prev: Function;
  category: string;
  showBigScreen: Function;
}

const offset = 6;

function Slider({
  movies,
  index,
  next,
  prev,
  category,
  showBigScreen,
}: ISlider) {
  const [back, setBack] = useState(false);
  const width = useWindowWidth();
  const [leaving, setLeaving] = useState(false);
  const toggleLeaving = () => {
    setLeaving((prev) => !prev);
  };

  const onPrevious = async () => {
    if (leaving) return;
    toggleLeaving();
    await setBack(true);
    prev();
  };
  const onNext = async () => {
    if (leaving) return;
    await setBack(false);
    toggleLeaving();
    next();
  };
  return (
    <Wrapper>
      <h1>{category} &rarr;</h1>
      <AnimatePresence
        initial={false}
        mode="sync"
        onExitComplete={toggleLeaving}
      >
        <Row
          key={index}
          initial={{ x: back ? -width + 10 : width - 10 }}
          animate={{ x: 0 }}
          exit={{ x: back ? width - 10 : -width + 10 }}
          transition={{ duration: 1, type: "just" }}
        >
          {movies.slice(offset * index, offset * (index + 1)).map((movie) => (
            <Card
              layoutId={category.slice(0, 3) + movie.id}
              onClick={() => showBigScreen(category.slice(0, 3), movie.id)}
              variants={cardVar}
              initial="initial"
              whileHover="hover"
              transition={{ type: "tween" }}
              key={category.slice(0, 3) + movie.id}
            >
              <motion.img
                src={getImg(movie.poster_path, "w500")}
                alt="poster"
              />
              <Info variants={infoVar}>
                <h4>{movie.title}</h4>
              </Info>
            </Card>
          ))}
          <SlideBtn
            whileHover={{ opacity: 1 }}
            style={{ position: "absolute", left: -60, zIndex: 10 }}
            onClick={onPrevious}
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
              <path d="M9.4 233.4c-12.5 12.5-12.5 32.8 0 45.3l192 192c12.5 12.5 32.8 12.5 45.3 0s12.5-32.8 0-45.3L77.3 256 246.6 86.6c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0l-192 192z" />
            </svg>
          </SlideBtn>
          <SlideBtn
            whileHover={{ opacity: 1 }}
            style={{ position: "absolute", right: -60, zIndex: 10 }}
            onClick={onNext}
          >
            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512">
              <path d="M310.6 233.4c12.5 12.5 12.5 32.8 0 45.3l-192 192c-12.5 12.5-32.8 12.5-45.3 0s-12.5-32.8 0-45.3L242.7 256 73.4 86.6c-12.5-12.5-12.5-32.8 0-45.3s32.8-12.5 45.3 0l192 192z" />
            </svg>
          </SlideBtn>
        </Row>
      </AnimatePresence>
    </Wrapper>
  );
}

export default Slider;
