import { useQuery } from "@tanstack/react-query";
import styled from "styled-components";
import {
  getImg,
  getMovie,
  getMovieDetails,
  getPopularMovies,
  getTopMovies,
  getUpcomingMovies,
  IMovie,
} from "../api";
import { AnimatePresence, motion, useScroll } from "framer-motion";
import { useState } from "react";
import { useHistory, useRouteMatch } from "react-router-dom";
import Details from "../Components/Details";
import Slider from "../Components/Slider";

const Wrapper = styled.div`
  width: 100%;
  height: 100%;
  overflow-x: hidden;
`;
const Loader = styled.div`
  font-size: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
`;

const Banner = styled.div<{ imgPath: string | undefined }>`
  width: 100vw;
  padding: 0 60px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-image: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0),
      rgba(0, 0, 0, 1)
    ),
    url(${(props) => props.imgPath});

  background-size: cover;
`;

const Title = styled.h1`
  font-size: 80px;
  margin-bottom: 15px;
`;

const Overview = styled.p`
  width: 35%;
  font-size: 23px;
`;

const Overlay = styled(motion.div)`
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  opacity: 0;
  position: fixed;
  top: 0;
`;
const DetailWrapper = styled(motion.div)`
  width: 40vw;
  height: 80vh;
  background-color: ${(props) => props.theme.black.darker};
  border-radius: 10px;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0 auto;
`;

const offset = 6;

function Home() {
  const [nowIndex, setNowIndex] = useState(0);
  const [popularIndex, setPopularIndex] = useState(0);
  const [topIndex, setTopIndex] = useState(0);
  const [upcomingIndex, setUpcomingIndex] = useState(0);
  const movieMatch = useRouteMatch<{ movieId: string }>("/movies/:movieId");
  // params type  TS params   .
  // movieMatch = {
  // isExact: true
  // params: {movieId: '280180'}
  // path: "/movies/:movieId"
  // url: "/movies/280180"
  // }
  const history = useHistory();
  const { data: movieData, isLoading: movieLoading } = useQuery<IMovie>({
    queryKey: ["movies", "now-playing"],
    queryFn: getMovie,
  });
  const { data: popularData, isLoading: popularLoading } = useQuery<IMovie>({
    queryKey: ["movies", "popular"],
    queryFn: getPopularMovies,
  });
  const { data: topData, isLoading: topLoading } = useQuery<IMovie>({
    queryKey: ["movies", "Top rated"],
    queryFn: getTopMovies,
  });
  const { data: upcomingData, isLoading: upcomingLoading } = useQuery<IMovie>({
    queryKey: ["movies", "Upcoming"],
    queryFn: getUpcomingMovies,
  });

  const prevSlide = () => {
    setNowIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextSlide = () => {
    if (movieData) {
      const maxIndex = Math.ceil(movieData?.results.length / offset) - 1;
      setNowIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const prevPopular = () => {
    setPopularIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextPopular = () => {
    if (popularData) {
      const maxIndex = Math.ceil(popularData?.results.length / offset) - 1;
      setPopularIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const prevTop = () => {
    setTopIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextTop = () => {
    if (topData) {
      const maxIndex = Math.ceil(topData?.results.length / offset) - 1;
      setTopIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const prevUpcoming = () => {
    setUpcomingIndex((prev) => (prev === 0 ? 0 : prev - 1));
  };
  const nextUpcoming = () => {
    if (movieData) {
      const maxIndex = Math.ceil(movieData?.results.length / offset) - 1;
      setUpcomingIndex((prev) => (prev === maxIndex ? 0 : prev + 1));
    }
  };
  const backToMain = () => {
    history.push("/");
  };
  const showBigScreen = (cat: string, movieId: number) => {
    history.push(`/movies/${cat}${movieId}`);
  };
  const { scrollY } = useScroll();

  return (
    <>
      {movieLoading ? (
        <Loader>
          <h1>Loading...</h1>
        </Loader>
      ) : (
        <Wrapper>
          <>
            <Banner imgPath={getImg(movieData?.results[0].backdrop_path || "")}>
              <Title>{movieData?.results[0].title}</Title>
              <Overview>{movieData?.results[0].overview}</Overview>
            </Banner>
            {movieData && (
              <Slider
                movies={movieData.results.slice(1)}
                index={nowIndex}
                next={nextSlide}
                prev={prevSlide}
                category={"Now Playing"}
                showBigScreen={showBigScreen}
              />
            )}
            {topLoading ? (
              <Loader>
                <h1> Loading...</h1>
              </Loader>
            ) : (
              topData && (
                <Slider
                  movies={topData.results}
                  index={topIndex}
                  next={nextTop}
                  prev={prevTop}
                  category="Top Rated"
                  showBigScreen={showBigScreen}
                />
              )
            )}

            {popularLoading ? (
              <Loader>
                <h1>Loading...</h1>
              </Loader>
            ) : (
              popularData && (
                <Slider
                  movies={popularData.results}
                  index={popularIndex}
                  next={nextPopular}
                  prev={prevPopular}
                  category={"Popular"}
                  showBigScreen={showBigScreen}
                />
              )
            )}
            {upcomingLoading ? (
              <Loader>
                <h1> Loading...</h1>
              </Loader>
            ) : (
              upcomingData && (
                <Slider
                  movies={upcomingData.results}
                  index={upcomingIndex}
                  next={nextUpcoming}
                  prev={prevUpcoming}
                  category="Upcoming"
                  showBigScreen={showBigScreen}
                />
              )
            )}

            <AnimatePresence>
              {movieMatch ? (
                <>
                  <Overlay
                    animate={{ opacity: 1 }}
                    exit={{ opacity: 0 }}
                    // without this, frame looks stiff.
                    onClick={backToMain}
                  />
                  <DetailWrapper
                    style={{ top: scrollY.get() + 50, overflowY: "auto" }}
                    layoutId={movieMatch.params.movieId}
                  >
                    <Details id={movieMatch.params.movieId.slice(3)} />
                  </DetailWrapper>
                </>
              ) : null}
            </AnimatePresence>
          </>
        </Wrapper>
      )}
    </>
  );
}

export default Home;
